.landingPageContainer{
  width:100vw;
  height:100vh;
  background:url("/public/background.png");
  background-repeat: no-repeat;
  background-size:cover;
  color:white;
}
.landingPageContainer nav{
padding:1.2rem;
padding-block:1.6rem;
display: flex;
justify-content: space-between;
align-items:center;
cursor:pointer;
}

.landingPageContainer nav h2{
 font-size:1.8rem;
 font-weight: 500;
}
.landingPageContainer nav .navlist{
display: flex ;
gap:1.6rem;
}
.landingMainContainer{
 
  display: flex;
  justify-content:space-between;
padding-inline:3rem;
height:auto;
align-items: center;
background-size:cover;


}
.landingMainContainer > div:nth-child(1){
font-size: 2.2rem;

}
.landingMainContainer > div:nth-child(1) div[role="button"]{
  background:#D97500;
  width:fit-content;
  padding:1rem;
  border-radius: 1.2rem;
  margin-top:1.4rem;
  cursor: pointer;
}
.landingMainContainer > div:nth-child(1) div[role="button"] a{
  text-decoration: none;
  color:white;
  font-size:1.7rem;

}

.landingMainContainer > div:nth-child(2) img{
 height:70vh;
 width:auto;

}